import React, { useState, useEffect } from "react";
import NavBar from "./components/NavBar/NavBar";
import User from "./components/User/User";
import Grid from "./components/UsersGrid/Grid";

const apiURL =
  "https://randomuser.me/api/?inc=gender,name,nat,location,picture,email&results=20";

function App() {
  const [focusedUser, setFocusedUser] = useState(null);
  const [users, setUsers] = useState(null);

  useEffect(() => {
    fetch(apiURL)
      .then((res) => res.json())
      .then((res) => {
        setUsers(res.results);
        setFocusedUser(res.results[0]);
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <div>
      <NavBar />
      {focusedUser != null && <User currentUser={focusedUser} />}
      {focusedUser != null && users != null && (
        <Grid
          users={users}
          callbackSetFocusedUser={(user) => setFocusedUser(user)}
          focusedUserId={focusedUser}
        />
      )}
    </div>
  );
}

export default App;
