import React from "react";
import Card from "./Card";

function Grid(props) {
  function handleCardClick(id) {
    const user = props.users.find((user) => {
      if (user?.email === id) return true;
      return false;
    });

    props.callbackSetFocusedUser(user);
  }

  function CreateCard(user) {
    return (
      <Card
        key={user?.email}
        id={user?.email}
        gender={user?.gender}
        nat={user?.nat}
        name={user?.name}
        email={user?.email}
        onClick={handleCardClick}
        focusedId={props.focusedUserId?.email}
      />
    );
  }

  return <div className="grid">{props.users.map(CreateCard)}</div>;
}

export default Grid;
