import React from "react";

function Card(props) {
  let str = props.user?.location?.timezone?.description;
  const arr = str.split(",");

  function description(place, index) {
    if (index === arr.length - 1) return <i key={index}>{place}</i>;
    else if (arr.length > 1 && index === arr.length - 2)
      return (
        <span key={index}>
          {" "}
          <u>{place.split(" ")[1] + ","}</u>
        </span>
      );
    return <span key={index}>{place + ","}</span>;
  }

  return (
    <div className="card-user">
      <img
        src={props.user?.picture?.large}
        alt="usr-img"
        className="card-user-circular-img"
      ></img>
      <div className="card-user-details">
        <div className="line-1">
          {props.user?.name?.title} {props.user?.name?.first}{" "}
          {props.user?.name?.last}{" "}
        </div>
        <div className="line-2">
          <span style={{ color: "#A259FF", fontWeight: "bold" }}>
            {props.user?.location?.street?.number}
          </span>
          {", "}
          {props.user?.location?.street?.name}
          {", "}
          {props.user?.location?.city}
          {", "}
          {props.user?.location?.state}
          {", "}
          <strong>{props.user?.location?.country}</strong>
          {", "}
          {props.user?.location?.postcode}
        </div>
        <div className="line-3">
          {props.user?.location?.timezone?.offset}
          {" - "}
          {arr.map(description)}
        </div>
        <div className="line-4">{props.user?.gender}</div>
      </div>
    </div>
  );
}

export default Card;
