import React from "react";

function Card(props) {
  let flag = false;
  if (props.focusedId === props.id) flag = true;

  return (
    <div
      className={flag ? "card-grid focused" : "card-grid"}
      onClick={() => props.onClick(props.email)}
    >
      <div className={flag ? "card-grid-line-1 focused" : "card-grid-line-1"}>
        {props.gender} â€¢ {props.nat}
      </div>
      <div className={flag ? "card-grid-line-2 focused" : "card-grid-line-2"}>
        {props.name?.title} {props.name?.first} {props.name?.last}
      </div>
      <div className={flag ? "card-grid-line-3 focused" : "card-grid-line-3"}>
        {props.email}
      </div>
    </div>
  );
}

export default Card;
